import React, { createElement, PropTypes } from 'react';
import { bindActionCreators } from 'redux';
import { Link } from 'react-router';

import { Panel, PanelHeader, PanelFooter, PanelBody } from 'react-weui';
import { MediaBox, MediaBoxTitle, MediaBoxDescription, MediaBoxInfo, MediaBoxInfoMeta, MediaBoxHeader, MediaBoxBody } from 'react-weui';
import { CellBody, CellFooter, CellHeader, FormCell, CellsTitle } from 'react-weui'
import { Radio, Switch, Checkbox, Form } from 'react-weui';
import { Msg, LoadMore, TextArea } from 'react-weui';

import './css/index.css';

class Empty extends React.Component {
  render () {
    return (
      <Msg
        type="info"
        description=""
      />
    );
  }
}

class Loading extends React.Component {
  render () {
    return <LoadMore loading></LoadMore>
  }
}

class Host extends React.Component {
  constructor () {
    super();

    this.state = {
      configure: null    
    };
  }

  componentDidMount () {
    const { fetch } = this.props;

    fetch()
      .then((res) => {
        return res.json();
      })
      .then((res) => {
        this.setState({
          configure: res.data 
        })
      });
  }

  panelsRender () {
    const { configure } = this.state;
    let list    = configure.rules,
        maps    = {},
        element = [];

    return list.map((k, i ) => {
      let panel = {
        tag: k.group,
        rules: k.rules,
        on: k.on
      }
      return this.singlePanelRender(panel, i)
    });
  }

  singlePanelRender (panel, i) {
    let content,
        row;

    content = panel.rules.map((u, j) => {
      let text = '',
          list;

      if (u.on) {
        text = '#';
      }

      list = typeof u.hostname == 'string' ? u.hostname : u.hostname.join('    ');

      text += `${u.ip}    ${list}`;

      return text;
    });

    row = content.length > 10 ? 10 : content.length;
    row = row < 5 ? 5 : row;

    content = content.join('\n');

    return (
      <Panel access key={i}>
        <Form checkbox className="app__midway-form">
          <FormCell checkbox className="app__midway-cell">
            <CellBody className="app__midway-cell-body">{panel.tag}</CellBody>
            <CellFooter>
              <Checkbox name="checkbox" value={i} defaultChecked checkbox={panel.on} onChange={() => this.onGroupChange(i)} />
            </CellFooter>
          </FormCell>
        </Form>
        <PanelBody>
          <MediaBox>
            <TextArea className="app__midway-textarea" rows={row}>{content}</TextArea>
            <MediaBoxInfo className="app__midway-box-info">
              <MediaBoxInfoMeta>
                <i className="iconfont icon-save app__midway-iconfont"></i>
              </MediaBoxInfoMeta>
              <MediaBoxInfoMeta>
                <i className="iconfont icon-remove app__midway-iconfont"></i>
              </MediaBoxInfoMeta>
            </MediaBoxInfo>
          </MediaBox>
        </PanelBody>
      </Panel>
    );
  }

  componentRender () {
    const { actions, dispatch } = this.props;
    const { configure } = this.state;

    if (configure) {
      let element;

      element = this.panelsRender();

      if (element.length === 0) {
        element = <Message />;
      }

      return (
        <div className="app__midway-host">
          {element}
        </div>
      );
    }

    return <Loading />;
  }

  render () {
    return this.componentRender();
  }
}

export default Host;


