import React, { createElement, PropTypes } from 'react';
import { bindActionCreators } from 'redux';
import { Link } from 'react-router';

import { Panel, PanelHeader, PanelFooter, PanelBody } from 'react-weui';
import { MediaBox, MediaBoxTitle, MediaBoxDescription, MediaBoxInfo, MediaBoxInfoMeta, MediaBoxHeader, MediaBoxBody } from 'react-weui';
import { CellBody, CellFooter, CellHeader, FormCell, CellsTitle, Cell, Cells } from 'react-weui';
import { Radio, Switch, Checkbox, Form } from 'react-weui';
import { Msg, Badge, Button, ButtonArea } from 'react-weui';

import './css/index.css';

class Loading extends React.Component {
  render () {
    return <LoadMore loading></LoadMore>
  }
}

class Protocol extends React.Component {
  constructor () {
    super();

    this.state = {
      http: [],
      https: []    
    };
  }

  componentDidMount () {
    const { connection } = this.props;

    connection.on('protocol', this.connectionHandle.bind(this));

    this.onClearTouch = this.onClearTouch.bind(this);
  }

  componentWillUnmount () {
    const { connection } = this.props;

    connection.off('protocol', this.connectionHandle.bind(this))
  }

  onClearTouch () {
    this.setState({
      http: [],
      https: []
    });
  }

  connectionHandle (data) {
    let { http, https } = this.state,
        isHttp = data.protocol == 'http:',
        state = {},
        protocol;        

    protocol = isHttp ? http : https;
    protocol.push(data);

    state[isHttp ? 'http' : 'https'] = protocol 

    this.setState(state);
  }

  counterRender (name, count, i) {
    return (
       <Cell access key={i} className="app__midway-protocol-cell">
          <CellBody>
            {name}
            <Badge preset="body">{count}</Badge>
          </CellBody>
          <CellFooter />
        </Cell>
    );
  }

  componentRender () {
    const { actions, dispatch } = this.props;
    const { http, https } = this.state;
    let protocolElement = [],
        total = http.length + https.length;

    protocolElement.push(
      this.counterRender('HTTP', http.length, 0)
    );

    protocolElement.push(
      this.counterRender('HTTPS', https.length, 1)
    );

    return (
      <div class="app__midway-protocol-cells">
        <Cells>
            <Cell class="app__midway-protocol-cell">
                <CellBody>
                    <p></p>
                    <p className="app__midway-protocol-desc">HTTP/HTTPS </p>
                </CellBody>
                <CellFooter>
                    <Badge preset="footer">{total}</Badge>
                </CellFooter>
            </Cell>
            {protocolElement}
        </Cells>
        <ButtonArea>
          <Button type="warn" onClick={this.onClearTouch}></Button>
        </ButtonArea>
      </div>
    );
   
  }

  render () {
    return this.componentRender();
  }
}

export default Protocol;


